.title{
text-align: center;
margin-top: 10px;
}
.list{
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 10px;
    padding: 20px;
    width: 370px;
    margin: 0 auto;
    box-sizing: border-box;
}
.item{
    border-radius: 5px;
    padding: 5px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    width: 100%;
    box-sizing: border-box;
}
.link{
    color:black;
}
.item:hover .link {
    color: rgb(35, 179, 131);
}